export const getSharePartById = (data) =>{
    const params = new URLSearchParams(data);
    return axios.post('BinaryStudio/handleShare/selectSharePartById',params)
    // return axios.post('/BinaryStudio/handleShare/selectAllSharePart')
}




//      ################################

service1.interceptors.request.use((config) =>{
    config.data= Object.assign({},config.data,{
        // authToken:window.localStorage.getItem('authToken')
        authToken:'myToken'

    })
    return config
})
service1.interceptors.response.use((resp)=>{
    if(resp.data.code === 200){
        return resp.data.data
    }else{
        // 处理错误
        message.error(resp.data.errMsg)
    }
})

ajax.interceptors.response.use((resp)=>{
    if(resp.status === 200){
        return resp
    }else{
        // 处理错误
        message.error(resp.data.errMsg)
    }
})


