export const getArticles = (offset,limited) =>{
    return service1.post('/api/binary/articleList',{
        offset,
        limited,
    })
}

//  上传时讯
export const uploadCapsule = (data) =>{
    const params = new URLSearchParams(data);
    //  若低版本浏览器使用不了  URLSearchParams
    // 参考：https://blog.csdn.net/qq_33603809/article/details/90521698
    return ajax.post('/handleCapsule/addTimeCapsule',params)
}


export const uploadArtifact = (data) =>{
    const params = new URLSearchParams(data);
    //   console.log('????? :',params)
    return axios.post('/BinaryStudio/handleArtifact/addArtifactInfo',params)
    // return axios.post('/BinaryStudio/handleShare/selectAllSharePart')
}
export const uploadSharePart = (data) =>{
    // const params = new URLSearchParams(data);
    //   console.log('????? :',params)
    console.log(data)
    var formData = new FormData()
    formData.append('part_title', data['part_title'])
    formData.append('part_img', data['part_img'])
    formData.append('part_url', data['part_url'])
   
   
    // for (var value of formData.values()) {
    //     console.log(value.valueOf());
    // }

    return axios.post('/BinaryStudio/handleShare/addSharePart',formData,{headers:{'Content-type':'multipart/form-data'}})
    // return axios.post('/BinaryStudio/handleShare/selectAllSharePart')
}




// 通过 id 删除文章
export const deleteArticleById = (id)=>{
    return service1.post(`/api/binary/articleDelete/${id}`)
}
// 通过ID获取文章
export const getArticleById = (id)=>{
    return service1.post(`/api/binary/article/${id}`)
}


// 保存文章
export const saveArticle = (id,data)=>{
    return service1.post(`/api/binary/articleEdit/${id}`,data)
}

// 管理员登录services
export const adminLoginRequest = (adminerInfo)=>{
    // console.log()
    return adminService.post('/api/binary/adminLogin',adminerInfo)
}

// 用户登录
export const userLoginRequest = (userInfo)=>{
    // console.log()
    return userService.post('/api/binary/userLogin',userInfo)
}

    