import React, { Component,createContext } from 'react'
// import {CartList} from './components'
import Home from '../views/Home'

import 'core-js/es/map';
import 'core-js/es/set';

import {BinaryFrame} from '../components'
import {Route,Switch,Redirect } from 'react-router-dom'

import {infoRoutes} from '../routes'


const menus = infoRoutes.filter(routes =>routes.isNav === true)

export default class InfoRoute extends Component {
  render() {
    return (
        <BinaryFrame menus={menus}>
          <Switch>
              {
                infoRoutes.map(route=>{
                  return <Route
                      key={route.pathname}
                      path={route.pathname}
                      exact={route.exact}
                      render = {(routeProps)=>{
                        return <route.component {...routeProps} />
                      }}
                  />
                })
              }
              <Redirect to={infoRoutes[0].pathname} from='/info' exact  />
              <Redirect  to='/404' />
          </Switch>
        </BinaryFrame>

    )
  }
}
