import React, { Component } from 'react'

import {getHandleArtifact} from '../../../services'
import  {Loading}  from '../../../components'
import {withRouter} from 'react-router-dom'


@withRouter
class ComputerItem extends Component {
    constructor(){
        super()
        this.state={
            dataSource:[],
            isLoading:false,
            isError:false,
        }
    }
    detailClick = (id)=>{
        this.props.history.push(`/info/stduioWorks/detailestduioWorks/:${id}`)
    }

    render() {
      
        return (
            <div className="row mid-img" style={{backgroundColor: "rgb(245, 245, 245)",minHeight:280}}>
                <div className="sec1200" style={{minWidth: "1024px"}}>
                    <div className="py-5 px-0">
                        <div className="row">
                            {
                                (
                                    this.props.isLoading
                                    ?
                                    <Loading/>
                                    :
                                    (
                                        this.props.dataSource.map( (item) => {
                                            return(
                                                <div className="col" key={item.artifact_id}>
                                                        <div className="card item"  onClick={this.detailClick.bind(item,item.artifact_id)}>
                                                            <div className="my_img_outer" >
                                                                <div style={{position: 'absolute',left: "0",right: "0",width: "100%",height: "100%"}}>
                                                                    <img className="card-img" src={item.artifact_img?item.artifact_img:'../image/01.jpg'} alt=""   />
                                                                    <div className="card-block">
                                                                        <div className="news-span">
                                                                            {item.artifact_name}
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                </div>
                                            )
                                        })
                                    )
                                )
                            }
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default ComputerItem
