import React, { Component } from 'react'
// import 'bootstrap/dist/css/bootstrap.min.css';
// import 'bootstrap/dist/js/bootstrap.min.js';
import {Spin}  from 'antd'

export default class MainImg extends Component {
    // headImgData= {...this.props}

    constructor(){
        super()
        this.state={
            headImgData:{},
            bigImgIsLoading:false,
            smImgIsLoading:false,
        }
    }
    componentDidMount(){
        this.setState({
            headImgData:{
                ...this.props,
                imgUrl: this.props.imgUrl?this.props.imgUrl:'url("../image/titleJpg2.jpg")',
                smImgUrl: this.props.smImgUrl?this.props.smImgUrl:'url("../image/titleSM.jpg")',
            }
        })

    }
    render() {
        return (
            <>
            <div className={"row"} style={{overflow: "hidden"}}>
            {/* background:this.state.headImgData.imgUrl */}
                <div ref={n =>{this.bigImg = n}} className={"full-section head-big-img head-big-img-home full-pic"} style={{height:this.state.headImgData.imgHeight,width: "100%"}}>
                    <div className="textBinary ">
                        {this.props.headTitle}
                    </div>
                </div>
                {/* background:this.state.headImgData.smImgUrl, */}
                <div className="head-sm-img head-sm-img-home" style={{backgroundPosition:" center center",width: "100%"}}>
                    <div className="textBinary">
                        {this.props.headTitle}
                    </div>
                </div>
            </div>
            </>
        )
    }
}
