import React, { Component,createContext } from 'react'

import {withRouter} from 'react-router-dom'

import $ from 'jquery'

import  './frame.less'
import {BinaryFotter}  from '../../views'



export  const {
    Provider,
    Consumer
  }= createContext()
  
  class ConsumerProvider extends Component{
    constructor(){
        super()
        this.state={
            artifactList:{},
            CapsuleList:{},
            sharePartList:{},
            inBinary:{},
            introduceBinary:{},
            binaryFotter:{
                contactUs:'联系我们'
            }
        }
    }
    render(){
        return(
            <Provider value={this.state}>
                {this.props.children}
            </Provider>
        )
    }
  }

  
  

@withRouter
class Frame extends Component {
    constructor(){
        super()
        this.state={
            nowPathName:'',
           
            }
        }
    onClickToRoute=(item)=>{
        this.props.history.push(item.pathname)
        // window.location.reload()
    }
     
    liNavbarCollpased = (e)=>{
      if($(e.currentTarget).parent().hasClass('show')){
            if(e.target.tagName ==='SPAN'){
                $(e.currentTarget).parent().removeClass('show')
      }
    }}
    
    render() {

        return (
            <ConsumerProvider>
                <div className="full-section" style={{overflow: "hidden"}}>
                    <div className="my_cover_absolute">
                        <nav className="full-section navbar head-font  navbar-expand-lg navbar-light" style={{backgroundColor:"white"}} >
                            
                                <img src="../image/icon.png" className="my_logo my_nav_hover" alt="logo" display="inline-block" onClick={ ()=>{ this.props.history.push('/info/home') } } />
                            
                            <button className="navbar-toggler "  type="button" data-toggle="collapse" data-target="#navbarTogglerDemo02" aria-controls="navbarTogglerDemo02" aria-expanded="false" aria-label="Toggle navigation">
                                <span className="navbar-toggler-icon"></span>
                            </button>
                            <div className={'collapse navbar-collapse' } id="navbarTogglerDemo02">
                            <ul className="ull navbar-nav mr-auto mt-2 mt-lg-0" onClick={this.liNavbarCollpased}>
                                {/* <li className="nav-item" > </li> */}
                                {
                                    this.props.menus.map(item=>{
                                        return(
                                            <li name={item.name}  className={this.props.location.pathname===item.pathname?'nav-item active my_nav_hover':'nav-item my_nav_hover'} key={item.pathname} onClick={this.onClickToRoute.bind(this,item)}>
                                            {/* <li className='nav-item active' key={item.pathname} onClick={this.onClick.bind(this,item)}> */}
                                                <span className='nav-link'> { item.title } </span>
                                            </li>
                                        )
                                    })
                                }
                                    <div className="row nav--link my_navLogin"  >
                                        <div className='col col-lg-5 offset-lg-6 col-md-5 my_nav_hover'>
                                            {/* <span onClick={()=>{  this.props.history.push('/adminlogin') }}>
                                                管理员
                                            </span>
                                            / */}
                                            <span onClick={()=>{  this.props.history.push('/userlogin') }}>
                                                管理员
                                            </span>
                                        </div>

                                    </div>
                            </ul>
                        </div>
                        </nav>
                    </div>
                    <div style={{height:"64px"}}></div>
                    {this.props.children}
                    <Consumer>
                        {
                            (arg)=>{
                                const {binaryFotter} = arg
                                return(
                                    <BinaryFotter binaryFotter={binaryFotter}/>
                                )
                            }
                        }    
                    </Consumer>
                </div>
            </ConsumerProvider>

        )}                   
}
export default Frame