import React, { Component } from 'react'
import moment from 'moment'
export default class CapsuleMobile extends Component {

    detailClick = (id)=>{
        this.props.history.push(`/info/capsules/detailecapsules/:${22}`)
    }
    render() {
        return (
            <div className="row mid-big-img pt-4">
                    <div className="f-content">
                        <span className="myNormalFont">
                                <h3> 时讯 </h3>
                        </span>
                    <div className="page-content">
                        <div className="years-box">
                            {/* <div class="years-box-title"><h1>时讯</h1></div> */}
                            <div className="years-box-content">
                                <div className="events-box">
                                    <div className="event-title">
                                        
                                    </div>
                                        {
                                            this.props.dataSource.map(item =>{
                                                return(
                                                    <div className="event   " key={item.capsule_id} onClick={this.detailClick.bind(item,item.capsule_id)}>
                                                        <div className="event-left">
                                                            <div className="event-sm-title ">
                                                                <h5>{item.capsule_title} </h5>
                                                            </div>
                                                            <div className="event-data">{ moment(Number(item.capsule_time)).format('YYYY-MM-DD  HH:mm')} </div>
                                                        </div>
                                                        <div className="right " style={{float: "right"}}></div>
                                                        <div className="both"></div>
                                                    </div>
                                                )
                                            })
                                        }
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
                    {/* this.props.dataSource.map(item =>{
                        return (
                            <span className="mid-big-img-a" key={item.capsule_id}>
                                <div className={"mid-big-img-a-left"}>
                                    {item.capsule_title}
                                </div>
                                <div className={"mid-big-img-a-right"}>
                                {   
                                                        moment(Number(item.capsule_time)).format('YYYY-MM-DD  HH:mm')
                                                    }  
                                </div>
                            </span>
                        )
                    }) */}
                
                </div>
    )}
}
