import React from 'react'

export default function MobileItem() {
    return (
        <div className="mobile-bd-home-bottom row">
            <div className="bd-bottom-share">
                <span>实时资讯请关注：</span>
                <div className="share-wechat">
                    <img src="../image/wechatIcon.png" className="share-wechat-img" alt="" />
                </div>
            </div>
            <div className="container mt-2 ml-2" style={{lineHeight: '1rem'}}>
                <div className="row" style={{display: 'block'}}>
                    <p style={{textIndent: '2rem'}}>
                        <span>联系地址：</span>
                        <span>XXXXXXXXXX</span>
                    </p>
                </div>
                <div className="row" style={{display: 'block'}}>
                    <p style={{textIndent: '2rem'}}>
                        <span className="tittle">联系电话</span>
                        <span>134330.....</span>
                    </p>
                </div>
            </div>
        </div>
    )
}
