import React, { Component } from 'react'
import { Layout, Menu, Breadcrumb, Icon,Dropdown } from 'antd';




import logo from './logo.png'
import   './frame.less'


import {withRouter} from 'react-router-dom'


const { Header, Content, Footer, Sider } = Layout;

// const menus = adminRoutes.filter(routes =>routes.isNav === true)
@withRouter
class Frame extends Component {

    onMenuClick = ({key})=>{
      this.props.history.push(key)
    } 
    onClick = ({ key }) => {
      console.log(key)
      switch(key){
        case '/info':
          this.props.history.push(key);
          window.location.reload();
          break;
        case '/info/home':
          this.props.history.push(key);
          window.localStorage.removeItem('adminAuthToken');
          window.sessionStorage.removeItem('adminAuthToken');
          window.location.reload();
          break;
        default:
          this.props.history.push('/userLogin')
      }
    };
    menu = (
      <Menu onClick={this.onClick}>
        <Menu.Item key="/info">
            返回首页
        </Menu.Item>
        <Menu.Item key="/info/home">
            注销
        </Menu.Item>
      </Menu>                  
    )

    render(){
      const selectedkeyArr = this.props.location.pathname.split('/')
      selectedkeyArr.length = 3
        return (
            <Layout>
            <Header className="header  my_header" >
              <div className="logo " >
                <img  className="my_logo"  src={logo}     alt="logo"/>
              </div>
              <Dropdown overlay={this.menu}>
                <div className="ant-dropdown-link" >
                  退出选项<Icon type="down" />
                </div>
              </Dropdown>
            </Header>
            <Content style={{ padding: '0 50px',}}>
              <Breadcrumb style={{ margin: '16px 0' }}>
              </Breadcrumb>
              <Layout style={{ padding: '24px 0', background: '#fff',minHeight:'500px' }}>
                <Sider width={150} style={{ background: '#fff' }}>
                  <Menu
                    mode="inline"
                    selectedKeys={ [selectedkeyArr.join('/')]}
                    // selectedKeys  的 花括号中 所接的内容，
                    // 会与  <Menu.Item> </Menu.Item>  组件中的 key 值 进行模糊匹配,即找出 key数组 值中匹配最符合的

                    //  当然 也可使用 [ this.props.location.pathname ]
                    //  使用 数组的方法，会直接 在 Menu.item 的 key 数组中寻找，
                    //  这里是依据  数组的索引  寻找 匹配
                    //  而去掉 方括号，会进行  key 数组键值的 模糊 匹配
                    //  
                    // [this.props.location.pathname]
                    onClick = {this.onMenuClick}
                    style={{ height: '100%' }}
                  >
                     {this.props.menus.map(item =>{
                        //  这里的 key 键值对，键名 = item.pathname  键值 = item.pathname
                        //  
                         return  <Menu.Item key={item.pathname} > <Icon type={item.icon} /> {item.title} </Menu.Item>
                        // return   <Menu.Item key={item.pathname} > <Link to={item.pathname}> <Icon type={item.icon} />  {item.title}  </Link> </Menu.Item>  
                     })}
                  </Menu>
                </Sider>
                <Content style={{ padding: '0', minHeight: 280 }}>{this.props.children}</Content>
              </Layout >
            </Content>
            <Footer style={{ textAlign: 'center' }}>Ant Design ©2018 Created by Ant UED</Footer>
          </Layout>
        )
    }
}
export default Frame