import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import {RightOutlined} from '@ant-design/icons'

const  routeArrData ={
    info:{
        name: '首页',
        pathname:'/info'
    },
    stduioWorks:{
        name: '作品',
        pathname:'/info/stduioWorks'
    },
    detailestduioWorks:{
        name: '作品详情',
        pathname:'/info/stduioWorks/detailestduioWorks/#'
    },
    capsules:{
        name: '时讯',
        pathname:'/info/capsules'
    },
    detailecapsules:{
        name: '时讯详情',
        pathname:'/info/capsules/detailecapsules/#'
    },
    shareParts:{
        name: '专题',
        pathname:'/info/shareParts'
    },
    detaileshareParts:{
        name: '专题详情',
        pathname:'/info/shareParts/detaileshareParts/#'
    },
    stduioIntroduce:{
        name: '工作室简介',
        pathname:'/info/stduioIntroduce'
    },
}
const patt = /detail/
export default class SmallNav extends Component {
    constructor(){
        super()
        this.state={
            navData:[],
            nowRoute:''
        }
    }
    componentDidMount(){
       this.setState({
           navData:this.props.smNavdata.navData,
           nowRoute:this.props.smNavdata.nowData
       })
    }

    render() {
        return (
            <div className="bg-light " style={{width:'100%',paddingLeft:'15px',marginLeft:'-15px'}}>
                <div className="smNav bg-light pl-sm-2" >
                    <Link to='/info/home'>
                        <span>
                            首页
                        </span> 
                    </Link>
                    {
                        this.props.smNavdata.navData.map(item=>{
                            return(
                                <div  className="d-inline-block"  key={item} >
                                    <RightOutlined style={{width:'2em'}} />
                                    {
                                        patt.test(item)
                                        ?
                                        routeArrData[item].name
                                        :
                                        <Link to={routeArrData[item].pathname}>
                                            {routeArrData[item].name}
                                        </Link>
                                    }
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        )
    }
}
