import React, { Component } from 'react'

import {getHandleArtifact} from '../../../services'

import ArtifactItem from './ArtifactItem'

export default class ArtifactList extends Component {
    constructor(){
        super()
        this.state ={
            dataSource:[
              {
                "artifact_id": 11,
                "artifact_name": "敬请期待"
              },{
                "artifact_id": 22,
                "artifact_name": "敬请期待"
              },{
                "artifact_id": 33,
                "artifact_name": "敬请期待"
              }],
            isLoading:false,
            isError:false
        }
    }

    deepClone(data) {
      let _data = JSON.stringify(data),
          dataClone = JSON.parse(_data);
      return dataClone;
    }

    limitShow = (respData)=>{
      //  已确认 respData 有接收到数据
      const addNum = ( respData.length >= 3) ? 3 : respData.length 
      let newDataSource = this.deepClone(this.state.dataSource)
      for (let i = 0; i < addNum; i++) {
        const element = respData[respData.length-addNum+i]
        newDataSource.unshift(element)
    }
      // 始终让数组在 3 个
      newDataSource.length = 3
      this.setState({
          dataSource: newDataSource
      })
    }
    componentDidMount(){
        this.getData()
    }
    getData = ()=>{
        this.setState({
          isLoading:true
        })
        getHandleArtifact()
        .then(resp =>{  
          this.limitShow(resp.data)
        }).catch(err=>{
           this.setState({
             isError:true
           })
        }).finally(err =>{
          this.setState({
            isLoading:false
          })
        })
      }
    render() {
        return (
            <>
                <ArtifactItem {...this.state} ></ArtifactItem>
                {/* <MobileItem {...this.state}></MobileItem> */}
            </>
        )
    }
}
