import React, { Component } from 'react'

import {withRouter,Link} from 'react-router-dom'


@withRouter
class IntroudeItem extends Component {

    constructor(){
        super()
        this.state={
           
        }
    }

    onClickToRoute=(item)=>{
        // console.log(item.pathname)

        this.props.history.push(item.pathname)
        // console.log(this.props.location)
    }
    render() {
        return (
        <div className="row company-section" style={{minWidth: "1024px"}} id="stduio_introduce">
            <div className="section">
                <div className="bd-home-company-desc">
                    <div className="section-title my-font">工作室介绍</div>
                    <div className="section-content company-section-content">
                        <div className="row">
                            {
                                this.props.dataSource.map(item =>{
                                    return (
                                        <div className="col" key={item.title}>
                                            {/* onClick={this.onClickToRoute.bind(this,item)} */}
                                            <div className="card bd-desc-item" onClick={this.onClickToRoute.bind(this,item)}>
                                                <div className="img-outer">
                                                    <div className="img-inner">
                                                        <img src={item.imgSrc} alt="" className="lazy"/>
                                                        <div className="card-cover">
                                                            {item.title}
                                                            <span className="bd-desc-titleline"></span>
                                                            <div className="bd-desc-content" dangerouslySetInnerHTML = {{ __html:item.content}}>
                                                            </div>
                                                            {/* <!-- <a href="/home/index/management_team" className="bd-desc-btn" target="_blank">了解更多</a> --> */}
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    )
                                })
                            }
                          
                        </div>
                    </div>
                </div>
            </div>
        </div>
        )
    }
}
export default IntroudeItem
