import React, { Component } from 'react'

export default class SharePartMobile extends Component {
    
    detailClick = (id)=>{
        this.props.history.push(`/info/shareParts/detaileshareParts/:${id}`)
    }
    render() {
        return (
            <div className="mid-big-img pt-2 bb-2 border-bottom border-dark ">
                <span className="p-2 myNormalFont">
                    <h3> 专题 </h3>
                </span>
                <div className="f-content  mb-4"  >
                    {
                        this.props.dataSource.map(item=>{
                            return(
                                <div className="sharePartItem" key={item.part_id} onClick={this.detailClick.bind(item,item.part_id)}>
                                    <div className="shartLeft">
                                        <img className="card-img" src={item.part_img?item.part_img:'../image/01.jpg'} alt=""/>
                                    </div>
                                    <div className="shartRight">
                                        {item.part_title?item.part_title:'暂无标题'}
                                        <div className="link">
                                            {item.part_url?item.part_url:'暂无链接'}
                                        </div>
                                    </div>
                                </div>
                            )
                        })
                    }
                </div>
            </div>

        )
    }
}
