import actionTypes from '../actions/actionTypes'
const isLogin = Boolean(window.localStorage.getItem('userAuthToken'))|| Boolean(window.sessionStorage.getItem('userAuthToken'))
const initState = {
    id: '',
    displayName:'',
    avtar:'',
    rule:'',
    isLogin,
    isLoading:false
}

export default (state=initState,action) =>{
    switch(action.type){
        case actionTypes.START_USERLOGIN:
            return{
                ...state,
                isLoading:true
            }
        case actionTypes.USERLOGIN_SUCCESS:
            return{
                ...state,
                ...action.payload.userInfo,
                isLoading:false,
                isLogin:true
            }
        case actionTypes.USERLOGIN_FAILED:
            return{
                initState
            }
        default:
            return state
    }
}