
import Loadable from 'react-loadable'
import {Loading}   from '../components'
import BinaryFotter  from './Home/BinaryFotter'

// 登录route
const AdminLogin = Loadable({
    loader:  () => import ('./Login/AdminLogin'),
    loading: Loading
})
const UserLogin = Loadable({
    loader:  () => import ('./Login/UserLogin'),
    loading: Loading
})
const NotFound = Loadable({
    loader:  () => import ('./NotFound'),
    loading: Loading
})

//  官网主页route
const Home = Loadable({
    loader:  () => import ('./Home'),
    loading: Loading
})
const InStduio = Loadable({
    loader:  () => import ('./InStduio'),
    loading: Loading
})
const StduioWorks = Loadable({
    loader:  () => import ('./stduioDynamic/StduioWorks'),
    loading: Loading
})

const ShowWorksItem = Loadable({
    loader: () => import('./stduioDynamic/StduioWorks/ShowWorksItem'),
    loading: Loading
})

const StduioIntroduce = Loadable({
    loader:  () => import ('./stduioDynamic/StduioIntroduce'),
    loading: Loading
})

const ShareParts = Loadable({
    loader: () => import ('./stduioDynamic/ShareParts'),
    loading: Loading
})
const ShowShareItem = Loadable({
    loader: () => import('./stduioDynamic/ShareParts/ShowShareItem'),
    loading: Loading
})

const Capsules = Loadable({
    loader: () => import ('./stduioDynamic/Capsules'),
    loading: Loading
})
const ShowCapsuleItem = Loadable({
    loader: () => import('./stduioDynamic/Capsules/ShowCapsuleItem'),
    loading: Loading
})


// 管理员 route 配置
// const Dashboard = Loadable({
//     loader:  () => import ('./Admin/Dashboard'),
//     loading: Loading
// })

const Settings = Loadable({
    loader:  () => import ('./Admin/Settings'),
    loading: Loading
})
// const ArticleList = Loadable({
//     loader:  () => import ('./Admin/Article'),
//     loading: Loading
// })
// const ArticleEdit = Loadable({
//     loader:  () => import ('./Admin/Article/Edit'),
//     loading: Loading
// })




const UserManageInfo = Loadable({
    loader: () => import ('./User/UserManageInfo'),
    loading: Loading
})

const Artifact = Loadable({
    loader: () => import ('./User/UserArtifact'),
    loading: Loading
})

const Capsule = Loadable({
    loader: () => import ('./User/UserCapsules'),
    loading: Loading
})

const SharePart = Loadable({
    loader: () => import ('./User/UserShareParts'),
    loading: Loading
})

const CapsulesEdit = Loadable({
    loader: () => import ('./User/UserCapsules/CapsulesEdit'),
    loading: Loading
})
const ArtifactEdit = Loadable({
    loader: () => import ('./User/UserArtifact/ArtifactEdit'),
    loading: Loading
})
const SharePartEdit = Loadable({
    loader: () => import ('./User/UserShareParts/SharePartEdit'),
    loading: Loading
})




const UserUpload = Loadable({
    loader: () => import ('./User/UserUpload'),
    loading: Loading
})

const UploadArtifact = Loadable({
    loader: () => import ('./User/UserUpload/UploadArtifact'),
    loading: Loading
})

const UploadCapsule = Loadable({
    loader: () => import ('./User/UserUpload/UploadCapsule/index'),
    loading: Loading
})

const UploadSharePart = Loadable({
    loader: () => import ('./User/UserUpload/UploadSharePart'),
    loading: Loading
})




//  Loadable  方法， 会将  loader 方法引用的组件存于高级组件（高级组件只体现  loader 引用的组件，其实就相当于是 loader 引用的组件 ）中并返回
//   并且  在高级组件中  会在  componentDidMount() 方法中对 loader 引用的组件 进行加载 并存于  state 中，
//   在高级组件  render 的内容中，会对  state 中的组件是否存在进行判断，判断是否 应该加载  Loading 组件

export {
    // 登录组件
    AdminLogin,
    UserLogin,

    // 官网信息组件
    Home,
    NotFound,
    InStduio,


    StduioWorks,
    ShowWorksItem,

    Capsules,
    ShowCapsuleItem,

    ShareParts,
    ShowShareItem,

    StduioIntroduce,
    BinaryFotter,

    // 管理员界面组件
    // Dashboard,
    Settings,
    // ArticleList,
    // ArticleEdit,

    //用户界面组件
    UserManageInfo,
    Artifact,
    Capsule,
    SharePart,
    Loading,

    UserUpload,
    UploadArtifact,
    UploadCapsule,
    UploadSharePart,

    CapsulesEdit,
    ArtifactEdit,
    SharePartEdit
}