import React, { Component } from 'react'
import { Layout,Menu, Breadcrumb,Dropdown,AlignLeftOutlined} from 'antd';
import {DownOutlined,MenuUnfoldOutlined} from '@ant-design/icons';
import { Form} from '@ant-design/compatible';
import '@ant-design/compatible/assets/index.css';



import logo from './logo.png'
import   './userFrame.less'


import {withRouter} from 'react-router-dom'


const { Header, Content, Footer, Sider } = Layout;
// const downIcon = props => <Icon component={downIcon} {...props} />;

// const menus = adminRoutes.filter(routes =>routes.isNav === true)
@withRouter
class Frame extends Component {
    constructor(){
      super()
      this.state={
        isCollapsed: false,
        isShowIcon: false,
      }
    }
    onMenuClick = ({key})=>{
      this.props.history.push(key)
      // if(this.state.isShowIcon){
      //   this.setState({
      //     isCollapsed:true
      //   })
      // }

    } 
    onBackClick = ({ key }) => {
      console.log(key)
      switch(key){
        case '/info':
          this.props.history.push(key);
          window.location.reload();
          break;
        case '/info/home':
          this.props.history.push(key);
          window.localStorage.removeItem('userAuthToken');
          window.sessionStorage.removeItem('userAuthToken');
          window.location.reload();
          break;
        default:
          this.props.history.push('/userLogin')
      }
    }
    onCollapse = (collapsed, type)=>{
      console.log('coll')
      if(type!=='clickTrigger'){
        // 判断是否是因为  窗口的宽度的改变。是 侧边栏  发生改变
        if(collapsed){
        //  窗口宽度改变导致 侧边栏收缩，  这时  isShowIcon 为 true ，即应该显示  trigger 图标
          this.setState({
            isShowIcon:true
          })
        }else{
          this.setState({
            isShowIcon:false
          })
        }
      }
      this.setState({
        isCollapsed:collapsed
      })
    }
    imgBackHome = (e)=>{
      e.preventDefault()
      this.props.history.push('/info')
    }
    menu = (
      <Menu onClick={this.onBackClick}>
        <Menu.Item key="/info">
            返回首页
        </Menu.Item>
        <Menu.Item key="/info/home">
            注销
        </Menu.Item>
        {/* <Menu.Divider /> */}
        {/* <Menu.Item key="3" disabled>
          3rd menu item（disabled）
        </Menu.Item> */}
      </Menu>                  
    )
   
    render(){
      const selectedkeyArr = this.props.location.pathname.split('/')
      selectedkeyArr.length = 3
        return (
            <Layout className="userFramLayout">
            <Header className="header  my_header" >
              <div className="logo " >
                <span  onClick={this.imgBackHome} href="#">
                  <img  className="my_logo"  src={logo}     alt="logo"  />
                </span>
               
              </div>
              <Dropdown overlay={this.menu}>
                <div className="ant-dropdown-link" >
                  退出选项 <DownOutlined/>
                </div>
              </Dropdown>
            </Header>
            <Content>
              <Breadcrumb  className="frameBread" >
              </Breadcrumb>
              <Layout  className="frameLayout" >
                <Sider 
                  collapsible 
                  collapsed={this.state.isCollapsed}
                  breakpoint="md"
                  collapsedWidth="0"
                  className='frameSide'
                  onCollapse = {this.onCollapse}
                  trigger={this.state.isShowIcon?<MenuUnfoldOutlined />:null}
                  theme="light"
                  >
                  <Menu
                    mode="inline"
                    selectedKeys={ [selectedkeyArr.join('/')]}
                    defaultSelectedKeys={'admin/info'}

                    onClick = {this.onMenuClick}
                    className="userFrameMenu"
                    theme="light"
                  >
                     {this.props.menus.map(item =>{
                        //  这里的 key 键值对，键名 = item.pathname  键值 = item.pathname
                         return  <Menu.Item key={item.pathname} > <item.icon/> <span>  {item.title} </span> </Menu.Item>
                        // return   <Menu.Item key={item.pathname} > <Link to={item.pathname}> <Icon type={item.icon} />  {item.title}  </Link> </Menu.Item>  
                     })}
                  </Menu>
                </Sider>
                <Content className="frameContent" >{this.props.children}</Content>
              </Layout >
            </Content>
            {/* <Footer style={{ textAlign: 'center' }}>Ant Design ©2018 Created by Ant UED</Footer> */}
          </Layout>
        )
    }
}
export default Frame