import React, { Component,createContext } from 'react'

import CompayDynamic from './CompayDynamic'
import  MainImg from './MainImg'
import ArtifactList from './ArtifactList'
import InBinary from './InBinary'
import IntroduceBinary from './IntroduceBinary'





export default class Home extends Component {
    constructor(){
        super()
        this.state={
            mainImgData: {
                imgUrl: 'url("../image/titleJpg2.jpg")',
                smImgUrl: 'url("../image/titleSM.jpg")',
                headTitle: '软件工作室',
                imgHeight: '30rem'
            }
        }
    }
    render() {
        return (
            <>
                <MainImg key="MainImg" {...this.state.mainImgData} />
                <ArtifactList key="ArtifactList" {...this.props} />
                <CompayDynamic key="CompayDynamic" {...this.props} />
                {/* <InBinary key="InBinary" /> */}
                <IntroduceBinary key="IntroduceBinary"  {...this.props} />
            </>

        )
    }
}
