/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { Component } from 'react'
import moment from 'moment'


export default class CapsuleItem extends Component {

    detailClick = (id)=>{
        this.props.history.push(`/info/capsules/detailecapsules/:${22}`)
    }
    render() {
        return (
            <div className="bd-dynamic-news">
                <div className="dynamic-title1">
                    时讯
                </div>
                <div className="news-list">
                    {
                        this.props.dataSource.map( (item)=>{
                            // return <CapsuleItem key={item.capsule_id}  {...item}/>
                            return(
                                    <div className="news-item" key={item.capsule_id} onClick={this.detailClick.bind(item,item.capsule_id)}>
                                        <div className="decoration">
                                            <div className="news-circle">
                                                <div className="solid-circle"></div>
                                            </div>
                                        </div>
                                        <div className="news-box">
                                            <span className="text-primary">
                                                <span  className="news-title my_nav_hover my_link_color" data-id="17931" >{item.capsule_title}</span>
                                                <div className="news-time"> 
                                                    {   
                                                        moment(Number(item.capsule_time)).format('YYYY-MM-DD  HH:mm')
                                                    }  
                                                </div>  
                                            </span>
                                        </div>
                                    </div>
                                
                            )
                        })
                    }
                </div>
                {/* <div className="more-news">
                    <span className="more-news-tip mb-3">
                    <Link to='/info/capsules'>
                        <Button type="primary"  shape="round" >
                            
                            更多
                            <img src="" alt=""/>
                        </Button>
                    </Link>
                    </span>
                </div> */}
        </div>
        )
    }
}
