import React, { Component } from 'react'

import CapsuleList from './CapsuleList'

import SharePartList from './SharePartList'
import CapsuleMobile  from './CapsuleList/CapsuleMobile'
import SharePartMobile from './SharePartList/SharePartMobile'
import {getHandleCapsule,getHandleShare} from '../../../services'


export default class CompayDynamic extends Component {
    constructor(){
        super()
        this.state={
            capsuleData:{
              isLoading: false,
              dataSource: [{
                capsule_id: '11',
                capsule_time: '',
                capsule_title:'敬请期待'
              }]
            },
            sharePartData:{
                isLoading: false,
                dataSource: [
                {
                    part_id:'11',
                    part_time: '',
                    part_title:'敬请期待'
                },{
                    part_id:'22',
                    part_time: '',
                    part_title:'敬请期待'
                },{
                    part_id:'33',
                    part_time: '',
                    part_title:'敬请期待'
                },{
                    part_id:'44',
                    part_time: '',
                    part_title:'敬请期待'
                }
                ]
              }
        }
    }

    componentDidMount(){
        this.getCapsuleData()
        this.getSharPartData()
        // this.setState({})
    }
    deepClone(data) {
        let _data = JSON.stringify(data),
            dataClone = JSON.parse(_data);
        return dataClone;
    }
    limitShow = (respData,dataSource)=>{
        // respData:    网上获取的数据
        // dataSource:  本地初始数据
        //  已确认 respData 有接收到数据
        const limitNum = 4
        const unshiftNum = ( respData.length >= limitNum) ? limitNum : respData.length 
        // unshiftNum   替换 dataSource 数组元素的个数
        var newDataSource = this.deepClone(dataSource)
        // 将获得的数据 
        for (let i = 0; i < unshiftNum; i++) {
          const element = respData[respData.length-unshiftNum+i]
          newDataSource.unshift(element)
        }
        // 始终让数组在 3 个
        newDataSource.length = 4
        return newDataSource
      }
    getCapsuleData = ()=>{
    this.setState({
        capsuleData:Object.assign({}, this.state.capsuleData, {isLoading:true })
    })
    getHandleCapsule()
    .then(resp =>{  
        const newData =  this.limitShow(resp.data,this.state.sharePartData.dataSource)
        this.setState({          
            capsuleData:Object.assign({}, this.state.sharePartData, {dataSource:newData }),
        })
    }).catch(err=>{

    }).finally(err =>{
        this.setState({
            capsuleData:Object.assign({}, this.state.capsuleData, {isLoading:false })
        })
    })
    }
    getSharPartData = ()=>{
        this.setState({
            sharePartData:Object.assign({}, this.state.sharePartData, {isLoading:true })
        })
        getHandleShare()
        .then(resp =>{  
            const newData =  this.limitShow(resp.data,this.state.sharePartData.dataSource)
            this.setState({          
                sharePartData:Object.assign({}, this.state.sharePartData, {dataSource:newData }),
            },()=>{
            })
        }).catch(err=>{
  
        }).finally(err =>{
          this.setState({
            sharePartData:Object.assign({}, this.state.sharePartData, {isLoading:false })
          })
        })
      }
    render() {
        return (
            <>
                <div className=" row dynamic-section minWidth" id="stduio_dynamic">
                    <div className={"section"} style={{backgroundColor: "rgb(255, 255, 255)"}}>
                        <div className="section-title my-font">
                            工作室动态
                        </div>
                        <div className={""}style={{paddingBottom:"58px"}}>
                            <div className={"row"} style={{backgroundColor: "rgb(245, 245, 245)",paddingBottom:"40px"}}>
                                <div className=" col-md-6 ">
                                    <CapsuleList {...this.state.capsuleData} {...this.props} />
                                </div>
                                <div className="col-md-6">
                                    <SharePartList  {...this.state.sharePartData} {...this.props} />
                                </div>  
                            </div>

                        </div>
                    </div>
                </div>
                <div className="container">
                    <SharePartMobile {...this.state.sharePartData} {...this.props}/>
                    <CapsuleMobile {...this.state.capsuleData} {...this.props}/>
                </div> 
            </>
        )
    }
}
