import React, { Component,createContext } from 'react'
// import {CartList} from './components'
import Home from './views/Home'

import 'core-js/es/map';
import 'core-js/es/set';

import {AdminRoute,InfoRoute,UserRoute } from './routeBlock'
// import {BinaryFrame} from './components'

 
// mainRoutes >>>  管理员登录  用户登录   404  
// 
import {mainRoutes} from './routes'
// import { BrowserRouter  as Router,Route,Switch,Redirect } from 'react-router-dom'
import {HashRouter as Router, Route,Switch,Redirect} from 'react-router-dom'

// const menus = infoRoutes.filter(routes =>routes.isNav === true)

export default class App extends Component {
  render() {
    // console.log(this.props)
    return (
      <Router>
      <Switch>
         {/* 官网信息的路由 */}
         <Route path='/info' key='./info' render={(routeProps)=>{
            return <InfoRoute {...routeProps} />
         }} />

         {/*  用户界面的路由 */}
         <Route path='/admin' key='./user' render={(routeProps)=>{
            return <UserRoute {...routeProps} />
         }} />

          {/*  管理界面的路由 */}
         {/* <Route path='/admin' key='./admin' render={(routeProps)=>{
            return <AdminRoute {...routeProps} />
         }} /> */}

         {/* 404  和  登录 的路由 */}
         {mainRoutes.map(route =>{
            return <Route  key={route.pathname} path={route.pathname} component={route.component} />
         })}
         {/* <Route key='/404' path='/404' component='NotFound' /> */}
         <Redirect to='/info' from='/' exact />
         <Redirect  to='/404' />
      </Switch>
   </Router>

    )
  }
}
